import React from 'react';

class Navbar extends React.Component{

    constructor(props){
        super(props);
        let hour = new Date().getHours().toLocaleString();
        let minute = new Date().getMinutes().toLocaleString();
        hour = this.check(hour);
        minute = this.check(minute);
        this.state = {
            time : hour+':'+minute
        };
    }

    componentDidMount(){
        this.intervalId = setInterval(
            ()=> this.tick()
            ,1000
        );
    }

    componentWillUnmount(){
        clearInterval(this.intervalId);
    }

    tick(){
        let hour = new Date().getHours().toLocaleString();
        let minute = new Date().getMinutes().toLocaleString();
        hour = this.check(hour);
        minute = this.check(minute);
        this.setState({
            time : hour+':'+minute
        });
    }

    check(value){
        if(value<10){
            return '0'+value;
        }
        return value;
    }


    render(){
        return(
            <div className='navbar'>
                <span className='left'><span>iPod</span><i className="fas fa-wifi" style={{marginLeft:5}}></i></span>
                <div style={{display: "inline-block ", fontWeight:'bolder'}}>{this.state.time}</div>
                <span className='right'>
                    { this.props.store.playing ? <i className="fas fa-play" style={{marginRight:5}}></i> : <i className="fas fa-pause" style={{marginRight:5}}></i> }
                    <img style={{width: 15 , position : 'relative', bottom : '-2px' }} alt='battery' src='data:image/png;base64,'></img></span>
            </div>
        );
    }
}

export default Navbar;